      <router-view/>
      <footers></footers>
      <jt></jt>
    </div>
  </div>
</template>

<script>
import jt from './components/jt.vue'
import navs from './components/nav.vue'
import footers from './components/footer.vue'
export default {
  name: 'App',
  components:{
    navs,footers,jt
  }
}
</script>

<style>
*{
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}
.container{
  padding: 0 80px;
}
</style>



