const getters =  {
	news(data){
		return data.news
	},
	sofas(data){
		return data.sofas
	},
	tables(data){
		return data.tables
	},
	desks(data){
		return data.desks
	},
	lamps(data){
		return data.lamps
	},
	xqsj(data,a){
		return data.xqsj
	},
	cart_arr(data){
		return data.cart_arr
	},
	xj(data){
		for (var i = 0; i < data.cart_arr.length; i++) {
			data.cart_arr[i].xj=data.cart_arr[i].sl*data.cart_arr[i].dj
		}
		return data.cart_arr
	}

}

const mutations = {
	sja(a,data){
		for (var i = 0; i < a.news.length; i++) {
			if(a.news[i].name==data){
				a.xqsj=a.news[i]
			}
		}
		for (var i = 0; i < a.sofas.length; i++) {
			if(a.sofas[i].name==data){
				a.xqsj=a.sofas[i]
			}
		}
		for (var i = 0; i < a.tables.length; i++) {
			if(a.tables[i].name==data){
				a.xqsjdesks=a.tables[i]
			}
		}
		for (var i = 0; i < a.desks.length; i++) {
			if(a.desks[i].name==data){
				a.xqsj=a.desks[i]
			}
		}
		for (var i = 0; i < a.lamps.length; i++) {
			if(a.lamps[i].name==data){
				a.xqsj=a.lamps[i]
			}
		}
	},
	 aaa(a,data){
	 	a.cart_arr.push({
	 		name:data.name,
	 		dj:data.jg,
	 		sl:1
	 	})
	 },
	 jy(a,data){
	 	a.cart_arr[data].sl<=1?a.cart_arr[data].sl=1:a.cart_arr[data].sl--
	 },
	 jiay(a,data){
	 	a.cart_arr[data].sl++
	 },
	 sc(a,data){
	 	a.cart_arr.splice(data,1)
	 }
}

const actions = {
	sj({commit},item){
		commit('sja',item)
	},
	jr({commit},item){
		commit('aaa',item)
	},
	j({commit},item){
		commit('jy',item)
	},
	jia({commit},item){
		commit('jiay',item)
	},
	shan({commit},item){
		commit('sc',item)
	}
}


export default new Vuex.Store({
	state,
	getters,
	mutations,
	actions
})















