<template>
	<div id="shoplist">
		<div class="list" v-for="item in data">
			<router-link :to="{name:'detail',params:{name:item.name}}"><img :src="item.img" alt=""></router-link>
			<h3>{{item.name}}</h3>
			<span>￥{{item.jg}}</span>
		</div>
	</div>
</template>
<script>
	export default{
		props:['data'],
		data(){
			return{
				arr:this.data
			}
		}
	}
</script>
<style>
	#shoplist .list{
		width: 25%;
		height: auto;
		float: left;
	}
	#shoplist .list img{
		width: 100%;
	}
	#shoplist .list h3{
		font-size: 12px;
		font-weight: normal;
	}
	#shoplist .list span{
      	font-size: 13px;
	    font-weight: normal;
	    color: #dd3333;
	    font-family: arial;
	}
</style>


