<template>
	<div id="detail">
		<div class="img">
			<img :src="xqsj.img" alt="">
		</div>
		<div class="text">
			<h3>{{xqsj.name}}</h3>
			<div class="jg">￥{{xqsj.jg}}</div>
			<p>纯手工定制，10%棉+70&麻，在商品设置中可以更改这里的商品简短描述，在商品设置中可以更改这里的商品简短描述</p>
			<button class="jr" @click="jr(xqsj)">加入购物车</button>
		</div>
	</div>
</template>
<script>
	import {mapGetters,mapActions} from 'vuex'
	export default{
		watch:{
			'$route'(to,from){
				console.log(to,from)
			}
		},
	    computed:mapGetters(['xqsj']),
		methods:mapActions(['sj','jr']),
		created(){
			this.sj(this.$route.params.name)
		}
	}
</script>
<style>
	#detail .img{
		width: 50%;
		float: left;
	}
	#detail .img img{
		width: 100%;
	}
	#detail .text{
		width: 49%;
		float: left;
	}
	#detail h3{
		font-size: 16px;
		font-weight: bold;
		border-bottom: 1px solid #666;
		padding: 20px 0;
	}
	#detail .jg{
	    font-size: 20px;
	    font-weight: bold;
	    color: #000000;
	    font-family: georgia;
	    line-height: 20px;
	}
	p{
		margin: 20px 0;
	}
	#detail .text button{
		width: 100px;
		height: 50px;
		background: orange;
		border: none;
		border-radius: 5px;
		display: block;
		margin-left: 30px;
		margin-top: 50px;
	}
</style>


