<template>
	<div class="jt">
		<div class="top" @click="ohide">
			在线客服
			<i class="el-icon-message"></i>
		</div>
		<div class="bottom">
			<div class="nh">
				您好，如有任何问题，请填写下面的表单。我们将会在第一时间联系您。
			</div>
			<div class="sy">
				使用QQ与我们交谈
			</div>
			<div class="qq">小桌子<a href="http://wpa.qq.com/msgrd?v=3&uin=657447446&site=qq&menu=yes"><img src="http://wpa.qq.com/pa?p=2:12345678:51" alt=""></a></div>
			<div class="qq">小椅子<a href="http://wpa.qq.com/msgrd?v=3&uin=657447446&site=qq&menu=yes"><img src="http://wpa.qq.com/pa?p=2:12345678:51" alt=""></a></div>
			<div class="qq">小凳子<a href="http://wpa.qq.com/msgrd?v=3&uin=657447446&site=qq&menu=yes"><img src="http://wpa.qq.com/pa?p=2:12345678:51" alt=""></a></div>
			<div class="rg">
				如果我们的QQ不在线，请填写表单
			</div>
			<div>
				<p>您的姓名</p>
				<input type="text" placeholder="您的姓名（必填项）">
			</div>
			<div>
				<p>邮箱地址</p>
				<input type="text" placeholder="邮箱地址（必填项）">
			</div>
			<div>
				<p>请告诉我们想咨询的事儿</p>
				<textarea name="" id="" cols="30" rows="5"></textarea>
			</div>
			<div>
				<button class="btn">发送</button>
			</div>
		</div>
	</div>
</template>
<script>
	export default{
		data(){
			return {
				num:0
			}
		},
		methods:{
			ohide(){
				this.num++;
				if (this.num%2){
					this.$el.style.bottom='0px'
				}else{
					this.$el.style.bottom='-440px'
				}
			}
		}
	}
</script>
<style>
	.jt{
		width: 300px;
		height: 480px;
		background: #fff;
		position: fixed;
		bottom: -440px;
		right: 50px;
		z-index: 99;
		transition: .3s;
	}
	.jt .top{
		width: 100%;
		height: 40px;
		background: #444;
		line-height: 40px;
		font-size: 14px;
		font-weight: normal;
		color: #fff;
		box-sizing: box;
		padding: 0 10px;
		border-radius: 8px 8px 0 0;
	}
	.jt .top i{
		float: right;
		line-height: 40px;
	}
	.jt .bottom{
		overflow: auto;
		width: 100%;
		height: 440px;
		box-sizing: box;
		padding: 0px 20px;
	}
	.nh{
		margin: 20px 0;
	}
	.qq{
		font-size: 12px;
		line-height: 34px;
	}
	.qq img{
		margin-left: 20px;
	}
	.rg{
		font-size: 12px;
		margin-top: 20px;
	}
	.jt .bottom p{
		font-size: 13px;
		line-height: 26px;
	}
	.jt .bottom input{
		width: 100%;
		height: 30px;
	}
	.jt div button{
		display: block;
		width: 50px;
		height: 30px;
		background: #3a99d1;
		color: #fff;
		border: none;
		border-radius: 5px;
		margin: 0 auto;
	}
</style>


