<template>
	<div id="cart">
	  <el-table
	    :data="xj"
	    style="width: 100%">
	    <el-table-column
	      prop="name"
	      label="商品名称"
	      width="180">
	    </el-table-column>
	    <el-table-column
	      prop="dj"
	      label="单价"
	      width="180">
	    </el-table-column>
	    <el-table-column
	      prop="sl"
	      label="数量"
	      width="180">
	    </el-table-column>
	    <el-table-column
	      prop="xj"
	      label="小计"
	      width="180">
	    </el-table-column>
	    <el-table-column
	      label="操作"
	      width="180">
		    <template slot-scope="scope">
			  <el-button type="danger" icon="el-icon-minus" size="mini" circle @click="j(scope.$index)"></el-button>
			  <el-button type="danger" icon="el-icon-plus" size="mini" circle @click="jia(scope.$index)"></el-button>
			  <el-button type="danger" icon="el-icon-delete" size="mini" circle @click="shan(scope.$index)"></el-button>
		    </template>
	    </el-table-column>
	  </el-table>
	  <button class="gm">
	  	前往收银台
	  </button>
	</div>
</template>
<script>	import {mapGetters,mapActions} from 'vuex'
	export default{
		data(){
			return{
			}
		},
		computed:mapGetters(['cart_arr','xj']),
		methods:mapActions({j:'j',jia:'jia',shan:'shan'})
	}
</script>
<style>
	#cart{
		margin-left: 100px;
		position: relative;
	}
	#cart .gm{
		padding: 8px 10px;
		position: absolute;
		bottom: -50px;
		right: 20px;
		border: none;
		background: yellowgreen;
	}
</style>


